<template>
  <ul class="clearfix">
    <li v-if="imgs.length>0" v-for='(item ,index ) in imgs'>
      <img :src="item.showPath">
      <button v-on:click="remove(item.extPath,index)">X</button>
    </li>
    <li style="position:relative" v-if="imgs.length>=6 ? false : true">
      <img src="../resources/logo.png"><input class="upload" ref="imgfile"  type="file">
    </li>
    <li @click='add_img' v-if="imgs.length>=6 ? false : true" >点击上传</li>
  </ul>
</template>

<script>
export default {
  data () {
    return {
      imgs: [],
      imgData: {
        accept: 'image/gif, image/jpeg, image/png, image/jpg',
      }
    }
  },
   methods: {
        add_img(event){
          let reader =new FileReader();
          let img1=this.$refs.imgfile.files[0];
         if(this.$refs.imgfile.files.length<1){
          console.log("请添加图片")
          return
         }
          let type=img1.type;//文件的类型，判断是否是图片
          let size=img1.size;//文件的大小，判断图片的大小
          if(this.imgData.accept.indexOf(type) == -1){
            alert('请选择我们支持的图片格式！');
            return false;
          }
          if(size>3145728){
            alert('请选择3M以内的图片！');
            return false;
          }
          var uri = ''
          let form = new FormData();
          form.append('file',img1,img1.name);
          this.$ajax.post(this.HOST+'/admin/upload',form,{
            headers:{'Content-Type':'multipart/form-data'}
          }).then(response => {
            this.imgs.push({
             showPath:response.data.showUrl,
             extPath:response.data.extPath
            });
            reader.readAsDataURL(img1);
            /*
            var that=this;
            reader.onloadend=function(){
             // that.imgs.push(uri);
              console.log(that.imgs)
            }
            */

          }).catch(error => {
            alert('上传图片出错！');
          })
        },
        remove:function(path,index){
           this.$ajax.post(this.HOST+'/admin/deleteImg',{
           'path':path
           }).then(response => {
                      this.imgs.splice(index,1);
                    }).catch(error => {
                      alert('删除图片出错！');
                    })










        }
   },

}
</script>

<style>
#table ul{
  display: flex;
  flex-direction:row;
  flex-wrap: nowrap;
  justify-content:space-between;
  margin:0 auto;
  padding:0;
  background-color:#f4f4f4;
}
#table li {
    list-style: none;
    text-align: center;
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    line-height: 1rem;
    font-size: 0.6rem;
}
</style>



